
"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import { Badge } from "@/components/ui/badge"
import {
  Users,
  Calendar,
  User,
  FileText,
  Briefcase,
  Ban,
  CreditCard,
  BarChart2,
  Settings,
  Bot,
  GraduationCap
} from "lucide-react"
import { useAuth } from "@/context/auth-context"
import { Role } from "@/lib/types"

const navItems = [
  { href: "/dashboard", label: "Schedule", icon: Calendar, roles: ["admin", "teacher", "upper-management", "high-level-dashboard"] },
  { href: "/dashboard/students", label: "Students", icon: Users, roles: ["admin", "teacher", "upper-management", "high-level-dashboard"] },
  { href: "/dashboard/ai-schedule-optimizer", label: "AI Scheduler", icon: Bot, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/ai-grade-suggester", label: "AI Grader", icon: GraduationCap, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/applicants", label: "Applicants", icon: User, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/requests", label: "Requests", icon: FileText, roles: ["admin", "high-level-dashboard"], badge: 3 },
  { href: "/dashboard/users", label: "Users", icon: Users, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/semesters", label: "Semesters", icon: Calendar, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/leaves", label: "Leaves", icon: Briefcase, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/exclusions", label: "Exclusions", icon: Ban, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/payments", label: "Payments", icon: CreditCard, roles: ["admin", "high-level-dashboard"] },
  { href: "/dashboard/reports", label: "Reports", icon: BarChart2, roles: ["admin", "upper-management", "high-level-dashboard"] },
  { href: "/dashboard/settings", label: "Settings", icon: Settings, roles: ["admin", "high-level-dashboard"] },
]

export function DashboardNav() {
  const pathname = usePathname()
  const { user } = useAuth()

  if (!user) return null

  const userHasRole = (roles: Role[]) => roles.includes(user.activeRole)

  return (
    <nav className="grid items-start gap-2">
      {navItems.map((item) =>
        userHasRole(item.roles) && (
          <Link key={item.href} href={item.href}>
            <span
              className={cn(
                "group flex items-center rounded-md px-3 py-2 text-sm font-medium hover:bg-accent hover:text-accent-foreground",
                pathname === item.href ? "bg-accent" : "transparent"
              )}
            >
              <item.icon className="mr-2 h-4 w-4" />
              <span>{item.label}</span>
              {item.badge && (
                <Badge className="ml-auto flex h-6 w-6 shrink-0 items-center justify-center rounded-full">
                  {item.badge}
                </Badge>
              )}
            </span>
          </Link>
        )
      )}
    </nav>
  )
}
