
"use client";

import React, { createContext, useContext, useState, useEffect, ReactNode } from 'react';
import { db } from '@/lib/firebase';
import { collection, onSnapshot } from 'firebase/firestore';
import { useAuth } from './auth-context';
import { Semester, StudentProfile } from '@/lib/types';
import { getInitialSemesters, getInitialStudents } from '@/lib/data'; // For initial structure, will be replaced by Firestore data

interface DatabaseContextType {
  semesters: Semester[];
  students: StudentProfile[];
  loading: boolean;
}

const DatabaseContext = createContext<DatabaseContextType | undefined>(undefined);

// Helper function to seed data. In a real app, this might be a separate script.
async function seedInitialData() {
  const { setDoc, doc, getDocs } = await import('firebase/firestore');
  
  try {
    console.log("Checking and seeding initial data to Firestore...");

    // Seed Semesters
    const initialSemesters = getInitialSemesters();
    const semesterCollection = collection(db, "semesters");
    const semesterSnapshot = await getDocs(semesterCollection);
    if (semesterSnapshot.empty) {
        console.log("Seeding semesters...");
        for (const semester of initialSemesters) {
            await setDoc(doc(db, "semesters", semester.id), semester);
        }
    }

    // Seed Students
    const initialStudents = getInitialStudents();
    const studentCollection = collection(db, "students");
    const studentSnapshot = await getDocs(studentCollection);
    if (studentSnapshot.empty) {
        console.log("Seeding students...");
        for (const student of initialStudents) {
            await setDoc(doc(db, "students", student.id), student);
        }
    }

    console.log("Seeding check complete.");
  } catch (error) {
    console.error("Error seeding data: ", error);
  }
}


export function DatabaseProvider({ children }: { children: ReactNode }) {
  const { user } = useAuth();
  const [semesters, setSemesters] = useState<Semester[]>([]);
  const [students, setStudents] = useState<StudentProfile[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // For demonstration, let's seed data if the collections are empty.
    seedInitialData();
  }, [])


  useEffect(() => {
    if (!user) {
        setSemesters([]);
        setStudents([]);
        setLoading(false);
        return;
    }

    setLoading(true);

    const unsubscribeSemesters = onSnapshot(collection(db, 'semesters'), (snapshot) => {
        const allSemesters = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as Semester));
        setSemesters(allSemesters);
        // We can turn off the main loading indicator once semesters are loaded
        setLoading(false); 
    }, (error) => {
        console.error("Error fetching semesters: ", error);
        setLoading(false);
    });

    const unsubscribeStudents = onSnapshot(collection(db, 'students'), (snapshot) => {
        const allStudents = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as StudentProfile));
        setStudents(allStudents);
    }, (error) => {
        console.error("Error fetching students: ", error);
    });


    // Cleanup listeners on component unmount
    return () => {
        unsubscribeSemesters();
        unsubscribeStudents();
    };
  }, [user]);

  const value = { semesters, students, loading };

  return <DatabaseContext.Provider value={value}>{children}</DatabaseContext.Provider>;
}

export function useDatabase() {
  const context = useContext(DatabaseContext);
  if (context === undefined) {
    throw new Error('useDatabase must be used within a DatabaseProvider');
  }
  return context;
}
